.card {
  width: 80%;
  height: 80%;
  padding: .05rem; }
  .card .svg-wrapper-backside {
    width: 100%;
    height: 100%;
    background: #8A2387;
    border: 1px solid #ffffff;
    border-radius: .5rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center; }
  .card .svg-wrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    border-radius: .5rem;
    width: 100%;
    height: calc(100% - 1px);
    background: -webkit-gradient(linear, right top, left top, from(#F27121), color-stop(#E94057), to(#8A2387));
    background: -webkit-linear-gradient(right, #F27121, #E94057, #8A2387);
    background: linear-gradient(to left, #F27121, #E94057, #8A2387);
    -webkit-transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
            transform: perspective(400px) rotate3d(0, 1, 0, 90deg);
    opacity: 0; }
    .card .svg-wrapper svg {
      cursor: pointer;
      color: #000000; }
  .card.matched .svg-wrapper-backside {
    background: red; }
  .card.matched .svg-wrapper {
    opacity: 1;
    -webkit-transform: none;
            transform: none; }
    .card.matched .svg-wrapper svg {
      cursor: not-allowed;
      color: #ffffff; }
  .card.selected .svg-wrapper {
    -webkit-animation: flipIn .5s ease-in;
            animation: flipIn .5s ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }
  .card.to-hide .svg-wrapper {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
    -webkit-animation: flipOut .5s ease-in-out;
            animation: flipOut .5s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

